@font-face {
  font-family: "Lato";
  font-weight: 400;
  display: swap;
  src: url("../fonts/Lato-400.woff2") format("woff2"), url("../fonts/Lato-400.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 700;
  display: swap;
  src: url("../fonts/Lato-700.woff2") format("woff2"), url("../fonts/Lato-700.woff") format("woff");
}
@font-face {
  font-family: "Open-Sans";
  font-weight: 400;
  display: swap;
  src: url("../fonts/Open-Sans-400.woff2") format("woff2"), url("../fonts/Open-Sans-400.woff") format("woff");
}
@font-face {
  font-family: "Open-Sans";
  font-weight: 700;
  display: swap;
  src: url("../fonts/Open-Sans-700.woff2") format("woff2"), url("../fonts/Open-Sans-700.woff") format("woff");
}
html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin-block-end: 0;
  margin-block-start: 0;
  margin-bottom: 0;
  margin-inline-end: 0;
  margin-inline-start: 0;
  /*Additions*/
  border: var(--c-body-border-outline);
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: "Lato", "Open-Sans", sans-serif;
  font-weight: 400;
  font-size: 12px;
  color: var(--c-body-txt);
  background-color: var(--с-body-bg);
  /*
  Доступно скрываю элементы
  */
  /*
  У секции добавляю вертикальные паддинги,
  это половина от расстояния между блоками
  */
}
body img, body picture {
  max-width: 100%;
  height: auto;
}
body button {
  padding: 0;
  cursor: pointer;
}
body a {
  text-decoration: none;
  color: inherit;
}
body ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
body h1,
body h2,
body h3,
body p,
body figure,
body fieldset {
  margin: 0;
}
body fieldset {
  padding-block-start: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
  padding-block-end: 0;
  min-inline-size: 0;
  border-width: 0;
  border-style: none;
}
body iframe {
  border: none;
}
body .visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
body section {
  max-width: var(--max-width-section);
  padding: var(--padding-section);
  /* padding-top 45 - 36~spare place from grid = 9 padding-top in section */
  margin: 0 auto;
}
body h1 {
  font-weight: 700;
}
body button {
  font-weight: 700;
}
body h2 {
  font-weight: 700;
}

.container:not(:last-child) {
  margin: 0 auto 45px auto;
}
@media (max-width: 1665px) {
  .container:not(:last-child) {
    margin: 0 auto 30px auto;
  }
  .main .container:not(:last-child) {
    margin-bottom: 45px;
  }
}
@media (max-width: 1023px) {
  .container:not(:last-child) {
    margin: 0 auto 25px auto;
  }
  .main .container:not(:last-child) {
    margin-bottom: 35px;
  }
}
@media (max-width: 690px) {
  .container:not(:last-child) {
    margin: 0 auto 21px auto;
  }
  .main .container:not(:last-child) {
    margin-bottom: 23px;
  }
}

.header__navigation {
  display: grid;
  max-width: var(--max-width-section);
  padding: var(--padding-header);
  margin: 0 auto;
  grid-template-columns: 190px 2fr auto 7fr auto 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  align-content: space-between;
  justify-items: center;
  grid-template-areas: "logo menu-btn . search-form . profile-btn cart-btn fav-btn";
}
@media (max-width: 1665px) {
  .header__navigation {
    grid-template-columns: 190px 104px 104px 104px auto 104px 104px 122px;
    grid-template-rows: 1fr;
    align-content: space-between;
    justify-items: center;
    row-gap: 34px;
    grid-template-areas: "logo . . . . profile-btn cart-btn fav-btn" "menu-btn search-form search-form search-form search-form search-form search-form search-form";
  }
}
@media (max-width: 1023px) {
  .header__navigation {
    grid-template-columns: 165px 73px 73px auto 73px 73px 73px 92px;
    grid-template-rows: 1fr;
    align-content: space-between;
    justify-items: center;
    row-gap: 29px;
    grid-template-areas: "logo . . . . profile-btn cart-btn fav-btn" "menu-btn search-form search-form search-form search-form search-form search-form search-form";
  }
}
@media (max-width: 690px) {
  .header__navigation {
    grid-template-columns: 114px auto 40px 40px 40px;
    grid-template-rows: 3fr;
    align-content: space-between;
    justify-items: center;
    row-gap: 20px;
    grid-template-areas: "logo . . . ." "menu-btn . profile-btn cart-btn fav-btn" "search-form search-form search-form search-form search-form";
  }
}
.header__logo {
  height: 54px;
  width: 190px;
  background: url("../img/__img-header/logo.svg") no-repeat center center;
  grid-area: logo;
  background-size: contain;
}
@media (max-width: 1023px) {
  .header__logo {
    height: 35px;
    width: 165px;
  }
}
@media (max-width: 690px) {
  .header__logo {
    height: 25px;
    width: 114px;
  }
}
.header__menu-btn {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 151px;
  height: 54px;
  background-color: #3670C7;
  border-radius: 10px;
  color: var(--c-body-txt);
  font-weight: 400;
  font-size: 22px;
  grid-area: menu-btn;
}
.header__menu-btn svg {
  margin-right: 7px;
}
@media (max-width: 1023px) {
  .header__menu-btn svg {
    margin-right: 4px;
  }
}
@media (max-width: 690px) {
  .header__menu-btn svg {
    margin-right: 3px;
  }
}
.header__menu-btn span {
  vertical-align: 5px;
}
@media (max-width: 1665px) {
  .header__menu-btn {
    justify-self: flex-start;
  }
}
@media (max-width: 1023px) {
  .header__menu-btn {
    width: 115px;
    height: 42px;
  }
}
@media (max-width: 690px) {
  .header__menu-btn {
    width: 79px;
    height: 30px;
    justify-self: flex-start;
  }
}
.header__menu-btn-wrp {
  padding-top: 5px;
}
@media (max-width: 1023px) {
  .header__menu-btn-wrp span {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
}
@media (max-width: 690px) {
  .header__menu-btn-wrp span {
    font-size: 12px;
    line-height: 18px;
    vertical-align: 7px;
  }
}
@media (max-width: 690px) {
  .header__menu-btn-wrp svg {
    width: 14px;
  }
}
.header__search-form {
  max-width: var(--search-form-width);
  height: 54px;
  display: grid;
  grid-template-columns: minmax(auto, calc(var(--search-form-width) - var(--search-btn-width))) var(--search-btn-width);
  grid-template-rows: 54px;
  grid-area: search-form;
}
@media (max-width: 1665px) {
  .header__search-form {
    justify-self: flex-end;
  }
}
@media (max-width: 1023px) {
  .header__search-form {
    height: 42px;
  }
}
@media (max-width: 690px) {
  .header__search-form {
    height: 30px;
    justify-self: center;
  }
}
.header__search-input {
  max-width: var(--search-form-width);
  height: 54px;
  background-color: #EFEFEF;
  border-radius: 30px;
  color: #8F8F8F;
  font-weight: 700;
  font-size: 16px;
  grid-column: 1/3;
  grid-row: 1/2;
  padding: 15px 0 18px 40px;
}
@media (max-width: 1023px) {
  .header__search-input {
    height: 42px;
    font-size: 12px;
    line-height: 16px;
  }
}
@media (max-width: 690px) {
  .header__search-input {
    height: 30px;
  }
}
.header__search-btn {
  width: var(--search-btn-width);
  height: var(--search-btn-height);
  background-color: #3670C7;
  border-radius: 30px;
  grid-column: 2/3;
  grid-row: 1/2;
  padding-top: 5px;
}
@media (max-width: 1023px) {
  .header__search-btn svg {
    width: 30px;
    height: 30px;
  }
}
@media (max-width: 690px) {
  .header__search-btn svg {
    width: 20px;
    height: 20px;
  }
}
@media (max-width: 1023px) {
  .header__search-btn {
    height: var(--search-btn-height);
    padding-top: 4px;
  }
}
@media (max-width: 690px) {
  .header__search-btn {
    height: var(--search-btn-height);
  }
}
@media (max-width: 690px) {
  .header__search-btn {
    padding-top: 3px;
  }
}
.header__right-btn-txt {
  font-size: 16px;
  line-height: 21px;
}
@media (max-width: 1023px) {
  .header__right-btn-txt {
    font-size: 12px;
    line-height: 16px;
  }
}
@media (max-width: 690px) {
  .header__right-btn-txt {
    display: none;
  }
}
.header__profile {
  height: 57px;
  max-width: 69px;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  grid-area: profile-btn;
  background-color: transparent;
  justify-self: flex-end;
}
.header__profile svg {
  width: 36px;
  height: 36px;
}
@media (max-width: 1023px) {
  .header__profile svg {
    height: 30px;
  }
}
@media (max-width: 690px) {
  .header__profile svg {
    height: 30px;
  }
}
@media (max-width: 1665px) {
  .header__profile {
    justify-self: flex-end;
  }
}
@media (max-width: 1023px) {
  .header__profile {
    height: 46px;
